.brk-highlite-primary-1 {
  position: relative;
  color: var(--brand-primary) !important;
  margin-left: 4px;

  &:after {
    content: '';

    position: absolute;
    left: -4px;
    top: -2px;
    width: calc(100% + 8px);
    height: calc(100% + 4px);

    background-color: rgba(var(--brand-primary-rgb), .3);
  }
}

.brk-highlite-primary-2 {
  text-transform: uppercase;
  text-decoration: underline;
  color: var(--brk-base-5) !important;
}

.brk-cpa-1 {
  position: relative;

  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 64px;

    @media screen and (max-width: 1230px){ 
      flex-direction: column;
      text-align: center;
    }
  }
}

.brk-cpa-2 {

  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 12px 0;
  }

  &__divider {
    margin-left: 25px;
    margin-right: 25px;
    width: 1px;
    height: 40px;
    background-color: rgba(256,256,256, .16);
    
    @media screen and (max-width: 768px){ 
      display: block;
      width: 100%;
      height: 1px;
      margin: 15px 0;
    }
  }
}

.brk-cpa-3 {
  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
  }
}

.brk-cpa-4 {
  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
  }
}
